import{Link, useNavigate}from 'react-router-dom'
import axios from'axios'
import { useInput, useUser } from "./hooks"
import { useEffect } from 'react'
import './login.css'
import {Button,Input} from 'antd'
import 'antd/dist/antd.css';



export default function Login(){
var name = useInput()
var password = useInput()
var navigate = useNavigate()
var userInfo = useUser()

useEffect(()=>{
  if(userInfo.data){
    navigate('/main')
  }
})//

async function login(){
var info = {
  name:name.value,
  password:password.value 
}
try{
  //axios
    var res= await axios.post('/account/login',info)//axios
    userInfo.update()
    navigate(-1)
}catch(e){
  let data = res.data
//400axios
alert(data.msg)
}
}


  return(
    <div className='login-box'>

      <div className='box1'>
      <div><Input placeholder='' type="text" {...name}/></div>
      <div><Input placeholder='' type="password" {...password}/></div>
      </div>
    <div className='box2'><Button type='primiry' shape='round' onClick={login}></Button> <Link to='/register'></Link></div>
    </div>
  )
}